import React from "react";
import { NavLink } from "react-router-dom";

const Common = (props) => {
  return (
    <>
      <section id="header" className="dcol-lg-0 order-lg-0">
        <div className="container-fluid  ">
          <div className="row">
            <div className="col-10 mx-auto">
              <div className="row">
                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                  <h1>
                    {props.name}
                    <strong className="brand-name"> Exception Motors </strong>
                  </h1>
                  <h2 className="my-3">{props.info} </h2>
                  <div className="mt-3">
                    <NavLink to={props.visit} className="btn-get-started">
                      Get started
                    </NavLink>
                  </div>
				  <div className="mt-4">
                    <NavLink to={props.visit} className="btn-get-sugg">
                      Reach Our Experts
                    </NavLink>
                  </div>
                </div>
                <br />
                <div className="col-lg-6 order-lg-2 header-img">
                  <img
                    src={props.imgsrc}
                    className="img-fluid animated"
                    alt="HOME img"
                    width="500"
                    height="200"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};
export default Common;
