import React from "react";
import Cardt from "./Cardt";

import Sdata1 from "./Sdata1";
const Service = (props) => {
  return (
    <>
      <div className="my-5">
        <h1 className="text-center">Our Partner Brands</h1>
        <h2 className="text-center orange-text">The best brands in the market are here</h2>
        <div className="container-fluid mb-5">
          <div className="row">
            <div className="col-10 mx-auto">
              <div className="row gy-8">
                {Sdata1.map((val, ind) => {
                  return (
                    <Cardt
                      key={ind}
                      imgsrc={val.imgsrc}
                      name={val.name}
                      link={val.link}
                      btn={val.btn}
                    />
                  );
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default Service;
