import React from "react";
import Card from "./Card";
import Sdata from "./Sdata";
const Account = (props) => {
  return (
    <>
      <div className="my-5">
	    <h1 className="text-center">The moto experts are here!!!</h1>
        <h2 className="text-center orange-text">Get Suggestions From Our Experts</h2>
        <div className="container-fluid mb-5">
          <div className="row">
            <div className="col-10 mx-auto">
              <div className="row gy-4">
                {Sdata.map((val, ind) => {
                  return (
                    <Card
                      key={ind}
                      imgsrc={val.imgsrc}
                      name={val.name}
                      spl={val.spl}
                      fav={val.fav}
                      btn={val.btn}
                    />
                  );
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default Account;
