import React from "react";
import personal from "../src/Web/datasvg.png";
import Common from "./Common";

const Home = (props) => {
  return (
    <>
      <Common
        name="Get you best motorbike choice with"
        visit="/service"
        imgsrc={personal}
        btname="Get Started"
        info="We are here to select the best motorbike for you!!!"
      />
    </>
  );
};
export default Home;
